/**
 * Tranzy - 网页自动翻译插件
 * 支持自动检测DOM更新并翻译新增元素
 * 使用微软翻译API实现多语言翻译
 * 同时支持ESM和浏览器直接使用
 *
 * @author Tranzy
 * @license MIT
 */

// 默认忽略的选择器列表，这些元素及其内容不会被翻译
const DEFAULT_IGNORE_SELECTORS = [
  'script',           // 脚本标签
  'style',            // 样式标签
  'noscript',         // 无脚本标签
  'code',             // 代码标签
  'pre',              // 预格式化文本标签
  'input',            // 输入框
  'textarea',         // 文本域
  '[contenteditable="true"]', // 可编辑元素
  '.tranzy-ignore'    // 自定义忽略类
];

// 默认配置选项
const DEFAULT_CONFIG = {
  toLang: navigator.language || '',    // 目标语言，默认从浏览器语言设置获取，如果没有则使用中文
  fromLang: '',                        // 源语言，如：'en'
  root: 'body',                        // 要翻译的根元素
  ignore: [],                          // 自定义忽略选择器列表
  force: [],                           // 强制翻译选择器列表
  doneClass: 'tranzy-done',            // 已翻译元素的标记类
  pendingClass: 'tranzy-pending',      // 正在翻译中的元素标记类
  auto: false,                         // 是否自动监听DOM变化
  batch: 100,                          // 批量翻译时的批次大小
  translator: null,                    // 自定义翻译器实例
  manualDict: {},                      // 手动翻译词典
  hooks: {                             // 生命周期钩子函数
    before: null,                      // 翻译开始前的钩子
    after: null,                       // 翻译结束后的钩子
    beforeElement: null,               // 翻译单个元素前的钩子
    afterElement: null,                // 翻译单个元素后的钩子
  }
};

// 辅助函数：深度合并对象，支持数组去重
function mergeDeep(...objects) {
  return objects.reduce((prev, obj) => {
    Object.keys(obj).forEach(key => {
      const pVal = prev[key];
      const oVal = obj[key];

      if (Array.isArray(pVal) && Array.isArray(oVal)) {
        // 数组类型进行合并并去重
        prev[key] = Array.from(new Set([...pVal, ...oVal]));
      } else if (isObject(pVal) && isObject(oVal)) {
        // 对象类型进行递归合并
        prev[key] = mergeDeep(pVal, oVal);
      } else {
        // 其他类型直接覆盖
        prev[key] = oVal;
      }
    });

    return prev;
  }, {});
}

// 辅助函数：检查值是否为对象（非null且非数组）
function isObject(item) {
  return (item && typeof item === 'object' && !Array.isArray(item));
}

// IndexedDB缓存管理器：用于存储翻译结果，提高性能
class TranslationCache {
  constructor() {
    this.dbName = 'tranzy-cache';      // 数据库名称
    this.storeName = 'translations';    // 存储对象名称
    this.db = null;                     // 数据库实例
    this.initPromise = this._initDatabase(); // 初始化Promise
  }

  // 生成字符串哈希值，用于缓存键
  _generateHash(str) {
    let hash = 2166136261;
    for (let i = 0; i < str.length; i++) {
      hash ^= str.charCodeAt(i);
      hash *= 16777619;
    }
    return hash.toString(36);  // 使用base36编码，生成更短的哈希值
  }

  // 初始化IndexedDB数据库
  async _initDatabase() {
    return new Promise((resolve, reject) => {
      const request = indexedDB.open(this.dbName, 1);

      request.onupgradeneeded = (event) => {
        const db = event.target.result;
        if (!db.objectStoreNames.contains(this.storeName)) {
          const store = db.createObjectStore(this.storeName, { keyPath: 'id' });
        }
      };

      request.onsuccess = (event) => {
        this.db = event.target.result;
        resolve();
      };

      request.onerror = (event) => {
        console.error('Tranzy: 初始化缓存数据库失败', event.target.error);
        reject(event.target.error);
      };
    });
  }

  // 获取缓存的翻译结果
  async get(text, toLang, fromLang = '') {
    await this.initPromise;
    const id = this._generateHash(`${text}|${toLang}|${fromLang}`);

    return new Promise((resolve) => {
      if (!this.db) {
        resolve(null);
        return;
      }

      const transaction = this.db.transaction(this.storeName, 'readonly');
      const store = transaction.objectStore(this.storeName);
      const request = store.get(id);

      request.onsuccess = (event) => {
        resolve(event.target.result?.value || null);
      };

      request.onerror = () => {
        resolve(null);
      };
    });
  }

  // 设置翻译结果到缓存
  async set(text, translation, toLang, fromLang = '') {
    await this.initPromise;
    const id = this._generateHash(`${text}|${toLang}|${fromLang}`);

    return new Promise((resolve) => {
      if (!this.db) {
        resolve();
        return;
      }

      const transaction = this.db.transaction(this.storeName, 'readwrite');
      const store = transaction.objectStore(this.storeName);
      const request = store.put({
        id,
        value: translation
      });

      request.onsuccess = () => {
        resolve();
      };

      request.onerror = () => {
        resolve();
      };
    });
  }

  // 批量获取缓存的翻译结果
  async getBatch(texts, toLang, fromLang = '') {
    await this.initPromise;
    const results = Array(texts.length).fill(null);
    const remainingIndices = [];

    await Promise.all(texts.map(async (text, index) => {
      const cachedTranslation = await this.get(text, toLang, fromLang);
      if (cachedTranslation) {
        results[index] = cachedTranslation;
      } else {
        remainingIndices.push(index);
      }
    }));

    return {
      results,
      remainingIndices
    };
  }

  // 批量设置翻译结果到缓存
  async setBatch(texts, translations, toLang, fromLang = '') {
    await this.initPromise;
    await Promise.all(texts.map((text, index) => {
      return this.set(text, translations[index], toLang, fromLang);
    }));
  }
}

// 默认微软翻译API实现
class MicrosoftTranslator {
  constructor(fromLang = '') {
    this.fromLang = fromLang;
    this.tokenExpireMinutes = 10; // token过期时间（分钟）
    this._loadTokenFromSession();
  }

  // 从sessionStorage加载token
  _loadTokenFromSession() {
    try {
      const tokenData = sessionStorage.getItem('tranzy_auth_token');
      if (tokenData) {
        const { token, timestamp } = JSON.parse(tokenData);
        const now = Date.now();
        // 如果token未过期，则使用缓存的token
        if (token && (now - timestamp) < this.tokenExpireMinutes * 60 * 1000) {
          this.authToken = token;
          this.tokenTimestamp = timestamp;
        } else {
          // token已过期，清除缓存
          this._clearTokenFromSession();
        }
      }
    } catch (error) {
      console.error('Tranzy: 从session加载token失败', error);
      this._clearTokenFromSession();
    }
  }

  // 保存token到sessionStorage
  _saveTokenToSession(token, timestamp) {
    try {
      sessionStorage.setItem('tranzy_auth_token', JSON.stringify({ token, timestamp }));
    } catch (error) {
      console.error('Tranzy: 保存token到session失败', error);
    }
  }

  // 清除sessionStorage中的token
  _clearTokenFromSession() {
    try {
      sessionStorage.removeItem('tranzy_auth_token');
      this.authToken = null;
      this.tokenTimestamp = 0;
    } catch (error) {
      console.error('Tranzy: 清除session中的token失败', error);
    }
  }

  // 获取微软翻译API的认证token
  async getAuthToken() {
    const now = Date.now();

    // 如果token获取时间小于10分钟，则直接返回
    if (this.authToken && (now - this.tokenTimestamp) < this.tokenExpireMinutes * 60 * 1000) {
      return this.authToken;
    }

    try {
      const response = await fetch('https://edge.microsoft.com/translate/auth', {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        }
      });

      if (!response.ok) {
        throw new Error(`获取微软翻译授权失败: ${response.status} ${response.statusText}`);
      }

      const token = await response.text();
      this.tokenTimestamp = now;
      this.authToken = token;
      
      // 保存token到session
      this._saveTokenToSession(token, now);
      
      return token;
    } catch (error) {
      console.error('Tranzy: 获取微软翻译授权失败', error);
      this._clearTokenFromSession();
      throw error;
    }
  }

  // 调用微软翻译API进行翻译
  async translate(texts, toLang) {
    try {
      // 过滤掉空文本和null值
      const filteredTexts = texts.filter(text => text && text.trim());

      // 如果没有有效文本需要翻译，直接返回空数组
      if (filteredTexts.length === 0) {
        return [];
      }

      // 获取认证令牌
      const token = await this.getAuthToken();

      // 构建URL，只在设置了fromLang时添加from参数
      const url = `https://api.cognitive.microsofttranslator.com/translate?${this.fromLang?`from=${this.fromLang}&`:''}&to=${toLang}&api-version=3.0`
        

      // 构建请求数据
      const data = filteredTexts.map(text => ({ Text: text }));

      // 发送请求
      const response = await fetch(url, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify(data)
      });

      if (!response.ok) {
        throw new Error(`翻译请求失败: ${response.status} ${response.statusText}`);
      }

      // 处理响应
      const result = await response.json();
      return result.map(item => item.translations[0].text);
    } catch (error) {
      console.error('Tranzy: 翻译请求失败', error);
      throw error;
    }
  }
}

// Tranzy核心类
class Tranzy {
  constructor(config = {}) {
    // 合并默认配置与用户配置
    this.config = mergeDeep({}, DEFAULT_CONFIG, config);

    // 标准化manualDict配置
    if (this.config.manualDict) {
      Object.keys(this.config.manualDict).forEach(lang => {
        const langDict = this.config.manualDict[lang];
        Object.keys(langDict).forEach(term => {
          // 如果翻译值是字符串，转换为标准格式
          if (typeof langDict[term] === 'string') {
            langDict[term] = {
              to: langDict[term],
              standalone: true,
              case: true
            };
          }
        });
      });
    }

    // 初始化缓存
    this.cache = new TranslationCache();

    // 初始化翻译器，传入用户配置的fromLang
    this.translator = this.config.translator || new MicrosoftTranslator(this.config.fromLang);

    // 初始化状态
    this.isTranslating = false;         // 是否正在翻译
    this.pendingElements = new Set();   // 待翻译元素集合
    this.observer = null;               // DOM观察器实例
    this.observerConfig = {             // 观察器配置
      childList: true,                  // 观察子节点变化
      subtree: true                     // 观察后代节点变化
    };
  }

  // 初始化插件
  init() {
    // 合并并去重忽略选择器
    this.config.ignore = [...new Set([
      ...DEFAULT_IGNORE_SELECTORS,
      ...(this.config.ignore || [])
    ])];

    // 确保强制翻译选择器是数组
    this.config.force = this.config.force || [];

    // 先进行一次初始翻译
    this.translatePage();

    // 如果配置了自动监听，则开始监听DOM变化
    if (this.config.auto) {
      this.startObserver();
    }

    return this;
  }

  // 开始观察DOM变化
  startObserver() {
    if (this.observer) {
      this.observer.disconnect();
    }

    this.observer = new MutationObserver((mutations) => {
      let shouldTranslate = false;
      for (const mutation of mutations) {
        // 处理新增节点
        if (mutation.type === 'childList' && mutation.addedNodes.length > 0) {
          mutation.addedNodes.forEach(node => {
            if (node.nodeType === Node.ELEMENT_NODE) {
              // 使用TreeWalker遍历所有元素节点
              const treeWalker = document.createTreeWalker(
                node,
                NodeFilter.SHOW_ELEMENT,
                {
                  acceptNode: function (node) {
                    // 如果元素匹配任何强制翻译选择器，则始终接受
                    if (this.config.force.some(selector => node.matches(selector))) {
                      return NodeFilter.FILTER_ACCEPT;
                    }

                    // 跳过被忽略的元素及其所有子元素
                    if (this.config.ignore.some(selector => node.matches(selector))) {
                      return NodeFilter.FILTER_REJECT;
                    }
                    return NodeFilter.FILTER_ACCEPT;
                  }.bind(this)
                }
              );

              // 检查根节点是否应该被处理
              if (this.config.ignore.some(selector => node.matches(selector)) &&
                !this.config.force.some(selector => node.matches(selector))) {
                return; // 如果根节点被忽略且不在强制翻译列表中，直接跳过整个树
              }

              // 添加根节点
              this.pendingElements.add(node);
              shouldTranslate = true;

              // 添加所有子元素节点
              let currentNode;
              while (currentNode = treeWalker.nextNode()) {
                this.pendingElements.add(currentNode);
              }
            }
          });
        }

        // 处理文本变化
        if (mutation.type === 'characterData') {
          const node = mutation.target.parentNode;
          if (node && node.nodeType === Node.ELEMENT_NODE) {
            // 检查所有父元素，如果有任何父元素被忽略且不在强制翻译列表中，则跳过
            let shouldSkip = false;
            let parent = node;

            while (parent) {
              if (this.config.ignore.some(selector => parent.matches(selector)) &&
                !this.config.force.some(selector => parent.matches(selector))) {
                shouldSkip = true;
                break;
              }
              parent = parent.parentNode;
              if (!parent || parent === document) break;
            }

            if (!shouldSkip) {
              this.pendingElements.add(node);
              shouldTranslate = true;
            }
          }
        }
      }

      if (shouldTranslate) {
        // 直接翻译
        this._translatePending();
      }
    });

    this.observer.observe(document.body, this.observerConfig);

    return this;
  }

  // 停止观察DOM变化
  stopObserver() {
    if (this.observer) {
      this.observer.disconnect();
      this.observer = null;
    }

    return this;
  }

  // 翻译待处理元素
  async _translatePending() {
    if (this.pendingElements.size === 0 || this.isTranslating) {
      return;
    }

    this.isTranslating = true;

    const elements = Array.from(this.pendingElements);
    this.pendingElements.clear();

    await this._translateElements(elements);

    this.isTranslating = false;

    // 检查是否有新的待处理元素
    if (this.pendingElements.size > 0) {
      this._translatePending();
    }
  }

  // 翻译整个页面
  async translatePage() {
    if (this.isTranslating) {
      return;
    }

    this.isTranslating = true;

    // 临时停止观察器，避免翻译过程中触发更多的变化
    const wasObserving = !!this.observer;
    if (wasObserving) {
      this.stopObserver();
    }

    // 执行beforeTranslate钩子
    if (typeof this.config.hooks.before === 'function') {
      await this.config.hooks.before();
    }

    try {
      // 使用TreeWalker获取所有需要翻译的元素
      const elements = [];
      const rootElement = document.querySelector(this.config.root);

      // 创建TreeWalker，只显示元素节点
      const treeWalker = document.createTreeWalker(
        rootElement,
        NodeFilter.SHOW_ELEMENT,
        {
          acceptNode: function (node) {
            // 跳过已翻译的元素
            if (node.classList.contains(this.config.doneClass)) {
              return NodeFilter.FILTER_SKIP; // 改为SKIP，允许检查子元素
            }

            // 跳过被忽略的元素及其所有子元素
            if (node.matches(this.config.ignore)) {
              // 如果元素匹配强制翻译选择器，允许翻译该元素
              if (this.config.force.some(selector => node.matches(selector))) {
                return NodeFilter.FILTER_ACCEPT;
              }
              return NodeFilter.FILTER_REJECT; // 忽略此元素及其所有子元素
            }

            // 如果元素匹配强制翻译选择器，则始终接受
            if (this.config.force.some(selector => node.matches(selector))) {
              return NodeFilter.FILTER_ACCEPT;
            }

            return NodeFilter.FILTER_ACCEPT;
          }.bind(this)
        }
      );

      // 收集所有符合条件的元素
      let node;
      while (node = treeWalker.nextNode()) {
        elements.push(node);
      }

      await this._translateElements(elements);
    } catch (error) {
      console.error('Tranzy: 页面翻译失败', error);
    } finally {
      // 执行afterTranslate钩子
      if (typeof this.config.hooks.after === 'function') {
        await this.config.hooks.after();
      }

      this.isTranslating = false;

      // 如果之前在观察，重新启动观察器
      if (wasObserving) {
        this.startObserver();
      }
    }

    return this;
  }

  // 翻译元素数组
  async _translateElements(elements) {
    // 过滤出需要翻译的元素
    const validElements = elements.filter(el => {
      // 跳过已经翻译过的元素
      if (el.classList.contains(this.config.doneClass)) {
        return false;
      }

      // 如果元素匹配强制翻译选择器，则始终翻译
      if (this.config.force.some(selector => el.matches(selector))) {
        return true;
      }

      // 检查所有父元素是否被忽略
      let parent = el.parentNode;
      while (parent && parent !== document) {
        if (parent.matches(this.config.ignore)) {
          return false; // 如果任何父元素被忽略且不在强制翻译列表中，则忽略此元素
        }
        parent = parent.parentNode;
      }

      // 跳过被忽略的元素
      if (el.matches(this.config.ignore)) {
        return false;
      }

      // 跳过没有文本内容的元素
      const text = this._getElementText(el);

      return text && text.trim().length > 0;
    });

    if (validElements.length === 0) {
      return;
    }

    // 批量处理元素
    const batchSize = this.config.batch;
    for (let i = 0; i < validElements.length; i += batchSize) {
      const batch = validElements.slice(i, i + batchSize);
      await this._translateElementBatch(batch);
    }
  }

  // 检查文本中是否包含术语并拆分
  _splitByTerms(text) {
    const toLangDict = this.config.manualDict[this.config.toLang];
    if (!toLangDict) return [text];

    // 按长度降序排序术语，优先匹配最长的术语
    const terms = Object.keys(toLangDict).sort((a, b) => b.length - a.length);
    
    // 先检查是否有完全匹配的术语
    for (const term of terms) {
      const translation = toLangDict[term];
      // 如果是独立匹配模式（默认为true）且文本完全等于术语
      if ((translation.standalone !== false) && 
          (translation.case === false ? text.toLowerCase() === term.toLowerCase() : text === term)) {
        return [text];
      }
    }

    // 如果没有完全匹配，且文本中没有包含任何术语，直接返回原文本
    if (!terms.some(term => {
      const translation = toLangDict[term];
      return translation.case === false ? 
        text.toLowerCase().includes(term.toLowerCase()) : 
        text.includes(term);
    })) {
      return [text];
    }

    // 拆分文本
    const parts = [];
    let currentText = text;
    let lastIndex = 0;

    while (currentText.length > 0) {
      let foundTerm = false;
      for (const term of terms) {
        const translation = toLangDict[term];
        // 跳过需要独立匹配的术语
        if (translation.standalone !== false) {
          continue;
        }

        // 使用正则表达式匹配术语，根据case配置决定是否忽略大小写
        const flags = translation.case === false ? 'gi' : 'g';
        const termRegex = new RegExp(`\\b${term}\\b`, flags);
        const match = termRegex.exec(currentText);
        
        if (match) {
          const index = match.index;
          // 如果术语前面有文本，添加为一部分
          if (index > 0) {
            parts.push(currentText.substring(0, index));
          }
          // 添加术语
          parts.push(term);
          // 更新剩余文本
          currentText = currentText.substring(index + term.length);
          foundTerm = true;
          break;
        }
      }
      // 如果没有找到术语，将剩余文本作为一部分
      if (!foundTerm) {
        parts.push(currentText);
        break;
      }
    }

    return parts;
  }

  // 批量翻译元素
  async _translateElementBatch(elements) {
    // 收集需要翻译的文本
    const elementsWithText = [];
    let textsToTranslate = [];

    for (const element of elements) {
      // 执行beforeElementTranslate钩子
      if (typeof this.config.hooks.beforeElement === 'function') {
        const shouldContinue = await this.config.hooks.beforeElement(element);
        if (shouldContinue === false) {
          continue;
        }
      }

      const text = this._getElementText(element);
      if (text && text.trim().length > 0) {
        // 简单的内容过滤，忽略纯数字、纯特殊字符等内容
        if (this._shouldTranslateText(text)) {
          elementsWithText.push(element);
          textsToTranslate.push(text);
          // 添加正在翻译的class
          element.classList.add(this.config.pendingClass);
        }
      }
    }

    if (textsToTranslate.length === 0) {
      return;
    }

    // 创建翻译结果映射表
    const translationMap = {};
    const termPartsMap = {}; // 记录需要按术语拆分的文本
    const textsToCheckCache = [];
    const finalTextsToFetch = new Set();

    // 收集所有需要翻译的文本
    for (const text of textsToTranslate) {
      // 检查文本中是否包含术语
      const parts = this._splitByTerms(text);
      
      // 如果只有一个部分，说明没有术语，直接检查完整文本
      if (parts.length === 1) {
        const toLangDict = this.config.manualDict[this.config.toLang];
        // 根据case设置决定如何匹配
        const translation = toLangDict && (
          toLangDict[text] || // 直接匹配
          (toLangDict[text.toLowerCase()]?.case === false ? toLangDict[text.toLowerCase()] : null) // 忽略大小写匹配
        );
        
        if (translation) {
          translationMap[text] = translation.to;
        } else {
          textsToCheckCache.push(text);
        }
      } else {
        // 如果有多个部分，记录需要按术语拆分的文本
        termPartsMap[text] = parts;
        // 将拆分出的部分添加到待翻译列表
        for (const part of parts) {
          const toLangDict = this.config.manualDict[this.config.toLang];
          // 根据case设置决定如何匹配
          const translation = toLangDict && (
            toLangDict[part] || // 直接匹配
            (toLangDict[part.toLowerCase()]?.case === false ? toLangDict[part.toLowerCase()] : null) // 忽略大小写匹配
          );
          
          if (translation) {
            translationMap[part] = translation.to;
          } else {
            const cachedTranslation = await this.cache.get(part, this.config.toLang, this.config.fromLang);
            if (cachedTranslation) {
              translationMap[part] = cachedTranslation;
            } else {
              finalTextsToFetch.add(part);
            }
          }
        }
      }
    }

    // 检查缓存（只检查未找到手动翻译的文本）
    const { results: cacheResults, remainingIndices: remainingIndices } =
      await this.cache.getBatch(textsToCheckCache, this.config.toLang, this.config.fromLang);

    // 将缓存结果合并到翻译映射表中
    let cacheIndex = 0;
    textsToCheckCache.forEach(text => {
      if (!(text in translationMap)) {
        translationMap[text] = cacheResults[cacheIndex++];
      }
    });

    // 收集需要调用API的文本
    remainingIndices.forEach(index => {
      finalTextsToFetch.add(textsToCheckCache[index]);
    });

    // 获取API翻译
    if (finalTextsToFetch.size > 0) {
      try {
        // 二次过滤不需要翻译的内容
        const filteredTexts = Array.from(finalTextsToFetch).filter(text => this._shouldTranslateText(text));
        
        // 将不需要翻译的文本直接设置为原文本
        Array.from(finalTextsToFetch).forEach(text => {
          if (!filteredTexts.includes(text)) {
            translationMap[text] = text;
          }
        });

        // 添加日志，帮助调试重复调用问题
        console.log('Tranzy: 调用API翻译', filteredTexts.length, '个文本');

        if (filteredTexts.length > 0) {
          const apiResults = await this.translator.translate(filteredTexts, this.config.toLang);

          // 将API翻译结果写入缓存
          await this.cache.setBatch(filteredTexts, apiResults, this.config.toLang, this.config.fromLang);

          // 将API结果合并到翻译映射表中
          let apiIndex = 0;
          filteredTexts.forEach(text => {
            translationMap[text] = apiResults[apiIndex++];
          });
        }
      } catch (error) {
        console.error('Tranzy: 批量翻译失败', error);
        Array.from(finalTextsToFetch).forEach(text => {
          translationMap[text] = text; // 发生错误时保持原文本
        });
      }
    }

    // 合并术语翻译结果
    for (const [originalText, parts] of Object.entries(termPartsMap)) {
      const translatedParts = parts.map(part => translationMap[part] || part);
      translationMap[originalText] = translatedParts.join('');
    }

    // 应用翻译
    elementsWithText.forEach((element, index) => {
      const originalText = textsToTranslate[index];
      const translatedText = translationMap[originalText];

      if (translatedText) {
        // 移除正在翻译的class
        element.classList.remove(this.config.pendingClass);
        this._applyTranslation(element, originalText, translatedText);

        // 执行afterElementTranslate钩子
        if (typeof this.config.hooks.afterElement === 'function') {
          this.config.hooks.afterElement(element, originalText, translatedText);
        }
      }
    });
  }

  // 判断文本是否值得翻译（过滤掉纯数字、纯符号等内容）
  _shouldTranslateText(text) {
    // 过滤掉空文本或只包含空白字符的文本
    if (!text || /^\s*$/.test(text)) {
      return false;
    }

    // 过滤掉只包含数字、空格、回车、特殊字符的文本
    // 但保留包含中文、英文、数字的文本
    if (/^[\s\d\p{P}\p{S}]+$/u.test(text) && !/[\p{L}\u4e00-\u9fa5]/.test(text)) {
      return false;
    }

    return true;
  }

  // 获取元素文本内容
  _getElementText(element) {
    // 额外的安全检查，确保script内容不会被处理
    if (element.tagName === 'SCRIPT' || element.closest('script')) {
      return null;
    }

    // 使用TreeWalker遍历元素的所有文本节点
    let textContent = '';
    const treeWalker = document.createTreeWalker(
      element,
      NodeFilter.SHOW_TEXT,
      {
        acceptNode: function (node) {
          // 跳过script标签内的文本节点
          if (node.parentNode.tagName === 'SCRIPT' || node.parentNode.closest('script')) {
            return NodeFilter.FILTER_REJECT;
          }

          // 只获取直接的文本节点，跳过深层元素内的文本
          if (node.parentNode !== element) {
            return NodeFilter.FILTER_SKIP;
          }
          return NodeFilter.FILTER_ACCEPT;
        }
      }
    );

    let node;
    while (node = treeWalker.nextNode()) {
      const text = node.textContent.trim();
      if (text) {
        textContent += text + ' ';
      }
    }

    return textContent.trim();
  }

  // 应用翻译
  _applyTranslation(element, originalText, translatedText) {
    if (!translatedText || originalText === translatedText) {
      return;
    }
    
    // 标记已翻译
    element.classList.add(this.config.doneClass);
    
    // 如果元素有子元素，只替换直接的文本节点
    if (element.childElementCount > 0) {
      const treeWalker = document.createTreeWalker(
        element,
        NodeFilter.SHOW_TEXT,
        {
          acceptNode: function(node) {
            if (node.parentNode !== element) {
              return NodeFilter.FILTER_SKIP;
            }
            return NodeFilter.FILTER_ACCEPT;
          }
        }
      );
      
      // 记录原始文本中的所有文本节点
      const textNodes = [];
      
      let node;
      while (node = treeWalker.nextNode()) {
        if (node.textContent.trim()) {
          textNodes.push(node);
        }
      }
      
      // 如果只有一个文本节点，直接替换
      if (textNodes.length === 1) {
        const leadingSpaces = textNodes[0].textContent.match(/^\s*/)[0];
        const trailingSpaces = textNodes[0].textContent.match(/\s*$/)[0];
        textNodes[0].textContent = leadingSpaces + translatedText + trailingSpaces;
      }
      // 如果有多个文本节点，我们需要更复杂的处理，这里简单处理为清空所有并在第一个放置原文
      else if (textNodes.length > 1) {
        // 清空所有文本节点
        for (let i = 1; i < textNodes.length; i++) {
          textNodes[i].textContent = '';
        }
        
        // 在第一个节点放置所有原始文本
        if (textNodes[0]) {
          const leadingSpaces = textNodes[0].textContent.match(/^\s*/)[0];
          const trailingSpaces = textNodes[0].textContent.match(/\s*$/)[0];
          textNodes[0].textContent = leadingSpaces + translatedText + trailingSpaces;
        }
      }
    } else {
      // 如果没有子元素，保留原始文本中的空格
      const leadingSpaces = element.textContent.match(/^\s*/)[0];
      const trailingSpaces = element.textContent.match(/\s*$/)[0];
      element.textContent = leadingSpaces + translatedText + trailingSpaces;
    }
  }
}

export default Tranzy; 